lled them ‘Bo-se’ in Korean. For these stores, we wondered how people could find them and their products without actually having to have to visit the store.\n                    ")]),_c('div',[_c('div',{staticClass:"portfolio-work-title subtitle-lg"},[_vm._v("Pain Point")])]),_c('div',[_vm._v("\n                        According to the research, a quarter of Koreans was shopping at non-branded clothing stores compared to branded stores. But, the problem is mostly all of the non-branded stores don’t have online stores unless they are major retails like Stylenanda or Imvely, while the branded stores have their own online websites. So, there was no way for customers to find and purchase the clothes that they have seen from offline stores online, and for store owners to offer their products to potential customers other than in the form of offline stores.\n                    ")])])])])]),_c('div',{staticClass:"portfolio-work-wrap"},[_c('div',{staticClass:"portfolio-work container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-4"},[_c('div',{staticClass:"portfolio-work-title subtitle-lg"},[_vm._v("Interviews")])]),_c('div',{staticClass:"offset-md-1 col-md-6"},[_vm._v("\n                    We have conducted user interviews to draw up an outline of our service. Excerpts from the Interviews are followed.\n                ")])]),_c('div',{staticClass:"row",staticStyle:{"display":"flex"}},[_c('div',{staticClass:"col-md-4 col-xs-12",staticStyle:{"margin":"10px","flex":"1","background-color":"#bbbbbb"}},[_c('div',{staticStyle:{"padding":"20px"}},[_c('div',{staticStyle:{"font-weight":"bold","font-size":"40px"}},[_vm._v("“")]),_c('div',[_vm._v("\n                            I live in the Hongdae area where you can find so many trendy fashion clothing stores. I love to shop there, but the thing is that it’s impossible to hit all the clothing stores there, so I never know what I am missing out on.\n                        ")])])]),_c('div',{staticClass:"col-md-4 co