. We realized that from the homepage the hamburger menu could hardly show users what other services and pages they can turn to unless they click and find the menu since it was hidden. Showing Stylefeed on the main page was one of the major features but at the same time, it was also important that we availed other tabs to allow users to explore around other stores.\n                        ")])]),_c('div',[_c('div',{staticClass:"portfolio-work-title subtitle-lg"},[_vm._v("Less Efficiency")]),_c('div',{staticStyle:{"margin-bottom":"200px"}},[_vm._v("\n                            With the former hamburger menu, it involved more clicks from users to find the pages and contents you were looking for whereas with the tab bar, you could find the contents simply with a single click. Furthermore, with the hamburger menu, it would only show on the main page which then would disappear once you click into the next page. Once you are on that page, you can’t find the hamburger menu unless you go back to the main page which then you will have to start again from the top, but with the tab bar, you can go back, but you can find where you need to be right away since the tab bars are fixed to be at the bottom.\n                        ")])])])])])]),_c('div',{staticClass:"portfolio-work-wrap"},[_c('div',{staticClass:"portfolio-work container"},[_c('div',{staticStyle:{"display":"flex","align-items":"center"}},[_c('div',{staticStyle:{"flex":"7"}},[_c('div',{staticClass:"portfolio-work-title subtitle-sm"},[_vm._v("Solution")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-4"},[_c('div',{staticClass:"portfolio-work-title subtitle-lg"},[_vm._v("Constant Live Feed from Favorite Stores")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6"},[_vm._v("\n                            Users are able to “pin” their favorite stores. Then from the favorite stores, users can receive updates on new items/sales instantly once it’s posted.\n                        ")])])]),_c